import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const forcastYrData = wutil.makeRandomData({ cnt: 500, min: 0, max: 25, onlyUp: false, inputData: [], diff: 1 });
const forcastStormData = wutil.makeRandomData({ cnt: 500, min: 0, max: 25, onlyUp: false, inputData: forcastYrData, diff: 1, maxSkew: 5 });
const aiData = wutil.makeRandomData({ cnt: 500, min: 0, max: 25, onlyUp: false, inputData: forcastYrData, diff: 1, maxSkew: 5 });
const windData = wutil.makeRandomData({ cnt: 500, min: 0, max: 25, onlyUp: false, inputData: aiData, diff: 1, maxSkew: 5, cutoff: 166 });

const initialState = {
	forcastYrData,
	forcastStormData,
	aiData,
	windData,
};

@observer
class Weather extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { windData, forcastYrData, forcastStormData, aiData } = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Weather</h1>
						<LineGraph
							stores={this.props.stores}
							data={windData}
							dataB={aiData}
							dataC={forcastStormData}
							dataD={forcastYrData}
							legend='Wind'
							legendB='Wind AI prediction'
							legendC='Wind forcast Yr'
							legendD='Wind forcast Storm'
							width={1200}
							height={500}
							yMax={30}
							yMin={0}
							showYTicks={1}
							yTicks='["30 m/s", "25 m/s", "20 m/s", "15 m/s", "10 m/s", "5 m/s", "0 m/s"]'
							unit='m/s'
							showXTicks={1}
							marker={400}
							avgWindow={8}
							avgWindowB={8}
							avgWindowC={8}
							avgWindowD={8}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Weather;
